<template>
	<div id="addStaff">
		<div class="page_title">
			<span @click="goBack">员工管理</span>
			<span class="blue">></span>
			<span class="blue">编辑员工信息</span>
		</div>
		<div class="staffCard">
			<div class="form_title">编辑员工信息</div>
			<div class="from_card">
				<el-form ref="form" :model="form1" label-width="100px" size="mini" :rules="rules" status-icon>
					<el-form-item label="店铺名称">
						<span>{{shopName}}</span>
					</el-form-item>
					<el-form-item label="职位" prop="position_name">
						<el-select v-model="form1.position_name" placeholder="职位">
							<el-option v-for="item in allDutyName" :label="item" :value="item" :key="item"></el-option>
						</el-select>
						<el-button class="add-btn" @click="addDuty">新增职位</el-button>
					</el-form-item>
					<el-form-item label="部门" prop="department_name">
						<el-select v-model="form1.department_name" placeholder="部门">
							<el-option v-for="item in allDepartName" :label="item" :value="item" :key="item"></el-option>
						</el-select>
						<el-button class="add-btn" @click="addNewDepartment">新增部门</el-button>
					</el-form-item>
					<el-form-item label="登录手机号" prop="inputPhone">
						<el-input v-model="form1.inputPhone" placeholder="请输入电话号码"></el-input>
						</el-tooltip>
					</el-form-item>
					<el-form-item label="登录验证码" prop="inputTest">
						<el-input v-model="form1.inputTest" placeholder="请输入验证码" class="phone" @change="checkPhoneCode(('form'))">
							<el-button class="add-btn-test" slot="append" @click="getTestNum" :disabled="cantGetTestNum">{{getTestNumBtn}}</el-button>
						</el-input>

					</el-form-item>
				</el-form>
				<div class="getInfor">
					<el-button v-if="success==false" :disabled="isDisable" class="getInfo">读取账号信息</el-button>
					<el-button @click="getCountInfor('form')" v-if="success" :disabled="isDisable" class="getInfoAble">读取账号信息</el-button>
				</div>
					</div>
				<div class="form_title">员工信息</div>
				<div class="from_card">
				<div class="inforCard" v-if="GetInfo">
					<el-form :model="form" label-width="100px" size="mini">
						<el-form-item label="姓名">
							<span>{{form.real_name}}</span>
						</el-form-item>
						<el-form-item label="身份证">
							<span>{{form.identity}}</span>
						</el-form-item>
						<el-form-item label="性别">
							<span>{{form.sex}}</span>
						</el-form-item>
						<el-form-item label="健康证扫描件">
							<img  class="img" :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+identyImg" v-if="identyImg&&identyImg!==''"/>
						<span v-else>未上传</span>
						</el-form-item>
						<el-form-item label="绑定微信">
							
							<span v-if="form.is_weixin == 1">未绑定</span>
							<span  v-if="form.is_weixin == 0">绑定</span>
						</el-form-item>
					</el-form>
				</div>
				
			</div>
			<div class="btnGroup">
					<el-button class="saveBtn" @click="saveInvitStaff('form')">保存</el-button>
					<el-button class="cancelBtn" @click="canselInvitStaff('form')">取消</el-button>
				</div>
			<div v-if="invited" class="invited">该号码已经被邀请</div>
		</div>

		<new-department :isShowDialog="isAddNewDepartment" @on-close="hideDialog" :allDepartName="allDepartName"></new-department>
	</div>
</template>

<script>
	import { staffDataGet } from "../../api";
	import { treeMethods } from "../../index";
	import { Public } from "@/config/public";
	import newDepartment from "./children/addNewDepartment";
	export default {

		data() {
			var checkPhone = (rule, value, callback) => {
				var reg = /^(0|86|17951)?(13[0-9]|15[012356789]|18[0-9]|14[57]|17[678])[0-9]{8}$/;
				if(!value) {
					callback(new Error('请输入电话号码'));
				} else if(reg.test(value) == false) {
					callback(new Error("请输入正确格式的手机号码"));
				} else {
					callback();
				}
			};
			var checkPhoneTest = (rule, value, callback) => {
				if(!value) {
					callback(new Error('请输入验证码'));
				} else if(value != this.testCode) {
					callback(new Error("请输入正确验证码"));
				} else {
					callback();
				}
			};
			return {
				imgbase_url: imgbase_url, //图片路径
				shopName: "",
				dutyForm: [], //获取职位列表得来的关于职位的数据对象，方便像后台传数据发送id
				departForm: [], //获取部门列表得来的关于部门的数据对象，方便像后台传数据发送id
				inputPhone: "", //输入的电话号码
				inputTest: "", //输入的验证码
				testCode: "",
				identyImg:"",
				allDepartName: [], //所有部门的名称，下拉框选择部门
				allDutyName: [], //所有职位名称，下拉框选择职位
				isAddNewDepartment: false, //添加新部门的模态框
				getTestNumBtn: "获取验证码",
				phoneArr: [],
				isAddedDuty: false,
				addedDuty: "",
				form1: {}, //职位，部门表单对象
				form: {}, //个人信息表单对象
				invited: false,
				isDisable: true, //是否可以点击
				cantGetTestNum: false, //s是否可以点击获取验证码
				success: false, //是否成功读取个人信息 
				GetInfo: false, //是否显示个人信息表单
				rules: {
					position_name: [{
						required: true,
						message: '请选择职位',
						trigger: 'click'
					}],
					department_name: [{
						required: true,
						message: '请选择部门',
						trigger: 'click'
					}],
					inputPhone: [{
						required: true,
						validator: checkPhone,
						trigger: 'blur,change'
					}],
					inputTest: [{
						required: true,
						validator: checkPhoneTest,
//						message: '请输入验证码',
						trigger: 'blur,change'
					}]
				}
			}

		},
		created() {
			this.getAllDepartName();
			this.getAllDutyName();
			this.getPhoneArr();
			this.getShopData()
		},
		components: {
			newDepartment
		},
		methods: {
			//获取店铺信息
			//获取店铺信息
			getShopData() {
				let shopId = Public.getShopid();
				let data = {
					get_shopinfo_base: 1,
					shop_id: shopId
				}
				staffDataGet.getShopData(data, (resp) => {
					this.shopName = resp.data.shopinfo.shop_name;
				})
			},
			//判断使用改号码的员工是否被邀请
			isInvited(phoneArr, phone) {

				phoneArr.forEach((item) => {
					if(item == phone) {
						this.invited = true
					}
				})

			},
			
			//点击获取验证码
			getTestNum() {
				if(this.form1.inputPhone !== "" && this.form1.inputPhone !== undefined) {
					var reg = /^(0|86|17951)?(13[0-9]|15[012356789]|18[0-9]|14[57]|17[678])[0-9]{8}$/;
					if(reg.test(this.form1.inputPhone) == true) {
						this.isInvited(this.phoneArr, this.form1.inputPhone);
						if(this.invited == false) {
							let data = {
								get_phone_code: 1,
								phone: this.form1.inputPhone
							}
							staffDataGet.getPhoneTest(data, (resp) => {
								if(resp.ret == 0) {
									this.testCode = resp.data.phone_code
									this.setIntervalMethods();
								} else if(resp.ret == -20050) {
									this.$slnotify({
										message: '该号码还未注册'
									});
								}
							})
						}
						setTimeout(() => {
							this.invited = false
						}, 2000)
					} else {
						return false;
					}
				} else {
					this.$slnotify({
			            message: "请输入电话号码"
			       });
					return;
				}
			},
			
			//检验输入验证码是否输入正确
			checkPhoneCode() {
					this.isDisable = this.form1.inputTest ==this.testCode ?false :true;
					if(this.isDisable == false) {
						this.success = true; 
					}else{
						this.success = false; 
					}
			},
			
			//获取账号信息
			getCountInfor(formName) {
				if(this.form1.inputPhone !== "" && this.form1.inputTest !== "") {
					this.$refs[formName].validate(valid => {
					if(valid) {
						this.isDisable = false;
					let data = {
						get_user_info: 1,
						phone: this.form1.inputPhone,
						phone_code: this.form1.inputTest
					}
					staffDataGet.getStaffList(data, (resp) => {
						if(resp.ret === 0) {
							this.GetInfo = true;
							this.identyImg = resp.data.user_info.health_certificate
							this.form = resp.data.user_info;
						} else {
							this.GetInfo = false;
							this.success = false;
						}
					})
					}
					})
				} else {
					this.$slnotify({
			            message: "请填写登录手机号以及获得的验证码"
			        });
			        return;
				}
			},

			//点击添加新部门
			addNewDepartment() {
				this.isAddNewDepartment = true;
			},
			//点击添加新职位
			addDuty() {
				sessionStorage.setItem("lastDuty", this.allDutyName[this.allDutyName.length - 1])
				this.isAddedDuty = true
				this.$router.push({
					path: '/staff/editDuty',
					query: {
						infor: this.allDutyName
					}
				})

			},
			//点击获取验证码发倒计时
			setIntervalMethods() {
				var clock = '';
				var nums = 60;
				this.cantGetTestNum = true; //将按钮置为不可点击
				this.getTestNumBtn = nums + '秒后可重新获取';
				clock = setInterval(() => {
					if(nums == 0) {
						clearInterval(clock); //清除js定时器
						this.cantGetTestNum = false;
						this.getTestNumBtn = '重新获取验证码';
						nums = 60; //重置时间
					} else {
						this.getTestNumBtn = nums + '秒后可重新获取';
						nums--;
					}
				}, 1000); //一秒执行一次

			},

			//获取部门列表
			getAllDepartName() {
				let data = {
					get_department_list: 1
				}
				staffDataGet.getDepartList(data, (resp) => {
					this.departForm = resp.data.department_list
					resp.data.department_list.forEach((item) => {

						if(this.allDepartName.indexOf(item.department_name) == -1) {
							this.allDepartName.push(item.department_name)
						}
					})
				})
			},

			//获取职位列表
			getAllDutyName() {
				let data = {
					get_position_list: 1
				}
				staffDataGet.getDutyList(data, (resp) => {
					this.dutyForm = resp.data.position_list
					resp.data.position_list.forEach((item, index) => {
						if(this.allDutyName.indexOf(item.position_name) == -1) {
							this.allDutyName.push(item.position_name)
						}
					})
					this.addedDuty = this.allDutyName[this.allDutyName.length - 1]
					let isNew = sessionStorage.getItem("lastDuty");
					if((isNew !== null && isNew != this.addedDuty)) {
						this.form1.position_name = this.addedDuty
					}
				})
			},
			//保存添加员工
			saveInvitStaff(formName) {
				sessionStorage.getItem("lastDuty") !== null ? sessionStorage.removeItem("lastDuty") : "";

				this.$refs[formName].validate(valid => {
					if(valid) {
						if(this.GetInfo == true) {
							if((this.form && this.form !== undefined) && (this.form1.inputPhone !== "" && this.form1.inputTest !== "")) {
								let position_id = treeMethods.getDutyDepartId(this.dutyForm, this.form1.position_name)
								let department_id = treeMethods.getDutyDepartId(this.departForm, this.form1.department_name)
								console.log(position_id)
								let data = {
									shop_employee_save: 1,
									user_id: this.form.userid,
									//									phone: this.form1.inputPhone,
									department_id: department_id,
									position_id: position_id
									//									real_name: this.form.real_name
								}

								staffDataGet.getUserInfor(data, this.goBack)

							} else {
								this.$slnotify({
						            message: "请填写完整信息"
						       });
							}
						} else {
							this.$slnotify({
								message: '请先获取员工信息'
							});
							return false;
						}
					}
				})

			},

			//获取手机号码数组
			getPhoneArr() {
				let myTel = Public.getShopinfo();
				this.phoneArr.push(myTel.userinfo.phone)
				staffDataGet.getStaffList({
					get_employee_list: 1
				}, (resp) => {
					resp.data.employee_list.forEach((item) => {
						this.phoneArr.push(item.phone)
					})
				})
			},

			//取消添加员工
			canselInvitStaff(formName) {
				this.$refs[formName].resetFields();
				this.goBack()
			},

			//隐藏弹框
			hideDialog(data) {
				this.form1.department_name = data !== "" && data !== undefined ? data : this.form1.department_name;
				this.allDepartName.push(data);
				this.getAllDepartName()
				this.isAddNewDepartment = false;

			},

			//返回员工管理页面
			goBack() {
				sessionStorage.getItem("lastDuty") !== null ? sessionStorage.removeItem("lastDuty") : "";
				this.$router.push("/shop/staff")
			}

		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	.staffCard {
		background: #ffffff;
		box-shadow: 0 2px 4px 0 #becaeb;
	}
	
	.from_card {
		width: 50%;
		padding: 48px 0 40px 0;
		margin-left: 133px;
	}
	
	.inforCard {
		width: 60%;
		margin-bottom: 30px;
		/*background: #F6F8FC;*/
	}
	
	.page_title {
		@include wh(100%, 16px);
		@include fc(12px, #333333);
		margin-bottom: 14px;
	}
	
	.form_title {
		@include wh(100%, 40px);
		@include fc(14px, #666666);
		line-height: 40px;
		background-color: #F6F8FC;
		padding-left: 14px;
	}
	
	.img {
		width: 100px;
		height: 50px;
	}
	
	.pinpo {
		font-size: 12px;
		color: #9B9B9B;
	}
	
	.blue {
		font-size: 12px;
		color: #4877E7;
	}
	
	.btnGroup {
		margin-top: 100px;
		height: 100px;
		padding-left: 45%;
	}
	
	.invited {
		position: absolute;
		top: 60%;
		left: 50%;
		width: 300px;
		height: 100px;
		line-height: 100px;
		text-align: center;
		color: #FFFFFF;
		border-radius: 4px;
		background: rgba(0, 0, 0, 0.5);
	}
	
	.getInfor {
		padding-left: 20%;
		margin-bottom: 10px;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#addStaff {
		.saveBtn.el-button,
		.add-btn.el-button {
			@include wh(160px, 35px);
			/*background: #4877e7;*/
			color: #4877E7;
		}
		.cancelBtn.el-button {
			@include wh(160px, 40px);
		}
		.getInfor .el-button {
			min-width: 120px;
		}
		.getInfoAble.el-button {
			border: 1px solid #4877E7;
			cursor: pointer;
			color: #4877E7;
			background: #FFFFFF;
			margin-left: 45%;
			margin-top: 20px;
		}
		.add-btn.el-button {
			margin-left: 5px;
			min-width: 100px;
			padding: 5px;
			border-radius: 4px;
		}
		.add-btn-test.el-button {
			margin-right: 5px;
			width: 120px;
			height: 24px;
			padding: 5px;
			background: #4877E7;
			color: #FFFFFF;
			border-radius: 4px;
		}
		.getInfo.el-button {
			border: 1px solid #D8D8D8;
			cursor: pointer;
			color: #FFFFFF;
			background: #D8D8D8;
			margin-left: 45%;
			margin-top: 20px;
		}
		.el-button {
			@include wh(160px, 40px);
			border: 1px solid #4877e7;
			color: #4877e7;
			text-align: center;
			border-radius: 5px;
			font-size: 12px;
		}
		.el-input-group__append {
			border-radius: 0;
			border-left: 0;
			padding-right: 0;
		}
		.phone .el-input__inner {
			border-right: 0;
		}
		.el-input__inner {
			border-radius: 0;
		}
		.el-input {
			width: 320px;
		}
		.inforCard {
			.el-form-item {
				margin-bottom: 35px;
			}
		}
		.el-button.is-disabled, .el-button.is-disabled:focus, .el-button.is-disabled:hover {
			background: #D8D8D8;
			border: 1px solid #D8D8D8;
		}
		.el-form-item .is-error .el-input-group__append{
			border: 1px solid #ff4949;
		}
	}
</style>


